export default {
  name: 'SelectBox',
  props: {
    name: {
      type: String,
      default: 'address',
    },
    placeholder: {
      type: String,
      default: '點擊展開選擇',
    },
    selectValue: {
      type: String,
      default: '',
    },
    options: {
      type: Array,
      default() {
        return []
      },
    },
  },
  data() {
    return {
      show: false,
      hasChosen: false,
      focus: false,
    }
  },
  mounted() {
    document.addEventListener('mousedown', (event) => {
      if (event.target.classList.contains('element')) return
      this.show = false
    })
  },
  methods: {
    handleInputSelect(value) {
      this.$emit('input', value)
      this.show = false
      this.hasChosen = true
    },
    handleClickButton() {
      this.show = !this.show
    },
  },
}
